import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes} from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { ChangeUserPasswordComponent } from './change-user-password/change-user-password.component';
import { CheckoutComponent } from './checkout/checkout.component';
import { HeaderComponent } from './header/header.component';
import { HomepageComponent } from './homepage/homepage.component';
import { LoginComponent } from './login/login.component';
import { PurchaseHistoryComponent } from './purchase-history/purchase-history.component';
import { ShoppingCartComponent } from './shopping-cart/shopping-cart.component';
import { SignupComponent } from './signup/signup.component';

import { UserService } from './services/user.service';
import { ShoppingCartService } from './services/shopping.cart.service';
import { LoadCoinInfoService } from './services/load.coin.info.service';
import { RouteService } from './services/route.service';

const appRoutes: Routes = [
  { path: 'home', component: HomepageComponent},
  { path: 'login', component: LoginComponent},
  { path: 'signup', component: SignupComponent},
  { path: 'purchase_history', component: PurchaseHistoryComponent, canActivate: [RouteService]},
  { path: 'cart', component: ShoppingCartComponent},
  { path: 'checkout', component: CheckoutComponent},
  { path: 'change_user_password', component: ChangeUserPasswordComponent},
  { path: '', redirectTo: '/home', pathMatch: 'full'}
]

@NgModule({
  declarations: [
    AppComponent,
    ChangeUserPasswordComponent,
    CheckoutComponent,
    HeaderComponent,
    HomepageComponent,
    LoginComponent,
    PurchaseHistoryComponent,
    ShoppingCartComponent,
    SignupComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [
    UserService,
    ShoppingCartService,
    LoadCoinInfoService,
    RouteService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



